.blog-pagination {
    text-align: center;

    &__wrapper {
        display: inline-grid;
        grid-template-columns: auto auto;
        grid-column-gap: 6rem;

        @include breakpoint(w-sm) {
            grid-column-gap: 1rem;
        }
    }

    &__link {
        font-size: 1.2em;
        height: 80px;
        width: 84px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: var(--color-charcoal-1);
        font-weight: bold;
        &--next {
            grid-column: 2;
        }

        &--prev {

        }

    }

}

.btn__wrapper.disabled,
.btn__wrapper.disabled a {
    pointer-events: none;
    opacity: 0.25;
    cursor: not-allowed;
}

