.bodystatic_flowRow{
    @extend %grid-6;
    &_step{
        grid-column: span 2;
        text-align: center;
        @include breakpoint(w-sm){
            grid-column: span 6;
            margin-bottom: 4rem;
        }
    }

    &_img{
        width: 100%;
        height: 220px;
        margin-bottom: 1.25rem;
        border: 5px solid $color-sunshine;
        border-radius: var(--roundcorner);
        background-color: $color-charcoal-3;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        position: relative;
        @include breakpoint(w-sm){
            height: 330px;
            margin: 0 auto 1.25rem auto;
        }
    }

    &_arrow{
        width: 6rem;
        height: 5px;
        background-color: $color-sunshine;
        position: absolute;
        top: 50%;
        right: -6rem;
        transform: translateY(-50%);
        &:after{
            content: "";
            border: solid $color-sunshine;
            border-width: 0 5px 5px 0;
            display: inline-block;
            padding: 10px;
            position: absolute;
            top: -10px;
            right: 0;
            transform: rotate(-45deg);
            -webkit-transform: rotate(-45deg);
        }
        @include breakpoint(w-med){
            width: 3rem;
            right: -3rem;
        }

        @include breakpoint(w-sm){
            display: none;
        }
    }
}